.factual-dd {
  height: 369px;
  width: 320px;
  border: 1px solid rgba(31, 32, 65, 0.3137254902);
  border-radius: 0 0 3px 3px;
  transform: translateY(5px);
  transition: transform 0.2s ease-out, opacity 0.15s ease-in, visibility 0s linear, height 0.2s ease-out;
  visibility: visible;
  margin-bottom: 20px;
  opacity: 1;
}
.factual-dd_extended {
  height: 409px;
  width: 320px;
  border: 1px solid rgba(31, 32, 65, 0.3137254902);
  border-radius: 0 0 3px 3px;
  transform: translateY(5px);
  transition: transform 0.2s ease-out, opacity 0.15s ease-in, visibility 0s linear, height 0.2s ease-out;
  visibility: visible;
  margin-bottom: 20px;
  opacity: 1;
}
.factual-dd_closed {
  border: 1px solid rgba(31, 32, 65, 0.3137254902);
  border-radius: 0 0 3px 3px;
  transform: translateY(-369px);
  transition: transform 0.2s ease-out, opacity 0.15s ease-in, visibility 0s linear 0.2s, height 0.2s ease-out;
  visibility: hidden;
  opacity: 0;
  height: 0px;
}
