.factual-dd__cell_highlighted {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(188, 156, 255, 0.25)), to(rgba(139, 164, 249, 0.25)));
  border-radius: 0;
}
.factual-dd__chosen {
  background: -webkit-gradient(linear, left top, left bottom, from(#bc9cff), to(#8ba4f9));
}
.factual-dd__chosen:hover {
  background: linear-gradient(180deg, rgba(188, 156, 255, 0.25), rgba(139, 164, 249, 0.25));
}
.factual-dd__chosen_first {
  background: -webkit-gradient(linear, left top, left bottom, from(#bc9cff), to(#8ba4f9));
  color: azure;
}
.factual-dd__chosen_first::after {
  content: "";
  position: absolute;
  height: inherit;
  width: inherit;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(188, 156, 255, 0.25)), to(rgba(139, 164, 249, 0.25)));
  border-radius: 50% 0 0 50%;
}
.factual-dd__chosen_last {
  background: -webkit-gradient(linear, left top, left bottom, from(#bc9cff), to(#8ba4f9));
  color: azure;
}
.factual-dd__chosen_last::after {
  content: "";
  position: absolute;
  height: inherit;
  width: inherit;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(188, 156, 255, 0.25)), to(rgba(139, 164, 249, 0.25)));
